"use client";
import { useRouter } from "next/navigation";

export default function Form({ title, fields, values, onChange, onSubmit, redirect }) {
  console.log(redirect)
  const router = useRouter();

  return (
    <div className="min-h-screen flex">
      {/* Lado esquerdo - Formul√°rio */}
      <div className="w-full bg-gray-900 text-white flex flex-col justify-center items-center px-10">
        <div className="bg-gray-800 p-8 rounded-lg shadow-lg w-full max-w-md">
          <h2 className="text-3xl font-bold mb-6 text-center">{title}</h2>
          <form onSubmit={onSubmit} className="space-y-4">
            {fields.map((field) => (
              <div key={field.name}>
                <label className="block text-gray-300 mb-1">{field.label}</label>
                <input
                  type={field.type || "text"}
                  name={field.name}
                  value={values[field.name] !== undefined ? values[field.name] : ""}
                  onChange={onChange}
                  required={field.required}
                  className="w-full p-3 border border-gray-600 bg-gray-700 rounded focus:outline-none focus:ring-2 focus:ring-yellow-500"
                />
              </div>
            ))}

            {/* Bot√µes de A√ß√£o */}
            <div className="flex gap-4">
              <button
                type="submit"
                className="w-full bg-yellow-500 text-gray-900 font-semibold p-3 rounded hover:bg-yellow-600 transition disabled:bg-gray-500"
              >
                Salvar
              </button>

              <button
                type="button"
                onClick={() => router.push('/clientes')}
                className="w-full bg-gray-600 text-white font-semibold p-3 rounded hover:bg-gray-700 transition"
              >
                Cancelar
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
